import React from 'react';
import styles from './NotesForm.module.scss';
import NotesArray from '../NotesArray';

const NotesForm = ({functionSubmit}) => (
    <div className={styles.form}>
    <form onSubmit={functionSubmit}>
        <h2>Dodaj notatkę</h2>
        <input placeholder="Tytuł" />
        <textarea placeholder="Treść notatki" maxLength="225"></textarea>
        <button className={styles.b1} type="submit">Dodaj</button>
    </form>
    </div>
)
export default NotesForm;