import React from 'react';
import styles from './Notes.module.scss';
import NotesArray from './NotesArray';
import Note from './Note/Note';
import NotesForm from './NotesForm/NotesForm';

class Notes extends React.Component {
    state = {
        array : [],
        addingform : true
    }

    formAppear = () => {
        if(this.state.addingform == false){
            this.setState({addingform : true});
        } else {
            this.setState({addingform : false})
        }
        
    }
     
    RemoveNote =(item1) =>{
       const newNotes = this.state.array.filter(noteitem =>{
            return noteitem !== item1;
       });
       this.setState({
           array : [...newNotes]
       })
    }

    AddNote = (e) => {
        e.preventDefault();
        const newItem1 = e.target[0].value;
        const newItem2 = e.target[1].value;
        let ar1 = [...this.state.array];
        ar1.push({h1 : newItem1, text : newItem2});
        this.setState({array : ar1}); 
    }


    render(){
        return(
        <>
            <div className={styles.wrapper}>
            <button className={styles.btn} onClick={this.formAppear}>Formularz</button>
            <div className={styles.page}>
                <div className={this.state.addingform ? styles.wrapper : styles.wideWrapper}>
                {
                        this.state.array.map(item => (
                            <Note header={item.h1} deleteFunc={(e) => this.RemoveNote(item)} text={item.text} />
                        ))
                }

                </div>
                <div className={this.state.addingform ? styles.AddingForm : styles.hiddenForm}>
                    <NotesForm functionSubmit={this.AddNote}/>
                </div>
            </div>
            </div>
         </>   
        )
    }
}
export default Notes;
