import React from 'react';
import { NavLink,BrowserRouter } from 'react-router-dom';
import styles from './Navbar.module.scss';

const Navbar = () => (  
   <>
        <ul className={styles.NavbarWrapper}>
            <li className={styles.logo}>
                <img src={require('../Notes/Note/note.png')}/>
                <h1>Your Notes</h1>
            </li>
            <li><h2><NavLink activeClassName={styles.linkActive} className={styles.link} exact to="/home">Przejdź do aplikacji</NavLink></h2></li>
            <li><h2><NavLink activeClassName={styles.linkActive} className={styles.link} exact to="/about">O projekcie</NavLink></h2></li>
            
        </ul>
   </>
)
export default Navbar;